import React from "react";
import coding from "../../coding.png";
export default function AboutMe() {
  return (
    <div
      className="bg-gradient-to-b from-cyan-700 to-cyan-500 py-20 about section"
      id="about"
    >
      <div className="flex text-slate-50 text-left lg:px-[10%] px-[8%] ">
        <div className="w-6/12 md:block hidden">
          <img src={coding} className="w-[100%]" alt="coding" />
        </div>
        <div className="sm:w-6/12 w-full">
          <h2 className="font-medium leading-tight text-5xl mt-10 text-left pb-0">
            About Me
          </h2>
          <hr className="w-64 h-1 bg-gray-200 border-0 rounded dark:bg-gray-700" />
          <img src={coding} className="w-[100%] md:hidden " alt="coding" />
          <h5 className="lg:text-lg md:text-md text-sm text-cyan-200 py-10" id="user-detail-intro">
            Aspiring Full Stack Web Developer capable of writing
            production-ready code using ReactJS, HTML, CSS, Chakra UI, React, Redux on the
            front end, Mongo DB, NodeJS, and Express on the backend to build single-page
            applications. 1200+ hours of coding and hands-on experience in
            developing various Web-Apps Looking forward to implement these
            skills in a well-motivated organization.
          </h5>
        </div>
      </div>
    </div>
  );
}
